  methods: {
    init() {
      let type = this.getQueryString("type");
      console.log("type...",type);
      if (type=="record"&&!this.$route.query.id){
        this.$router.push('/record');
        return false;
      }
      //
      this.getBuildTypeList();
      //
      this.getInitData();
    },
    getInitData() {
      // let data = this.$route.query.data;//
      let signId = this.$route.query.id;//id
      // this.recordId = this.$route.query.recordId;//id
      if (signId == 1){
        console.log("");
        this.showBtns = false;
        this.viewDisabled = true;
        this.form.rentWayList[0].disabled = true;
        this.form.rentWayList[1].disabled = true;
        this.saveDraft = false;
        this.createCurrentData = false;
        this.id=this.$route.query.recordId;//Id
        this.getDataByPointId(1);
      } else if (signId == 2){
        console.log("");
        this.id=this.$route.query.recordId;//Id
        this.getDataByPointId(2);
      } else {
        console.log("");
      }
    },
    //ID
    getDataByPointId(singId){
      axios.get('/point-evaluation/update?point_id='+this.id).then((res)=>{
          const initData =res.data;
          console.log("pointinit",initData);
          if (initData.error_code== 0){
            if (initData.data.isUpdate==0&&singId==2) {
              this.$router.push({name:"pointContact",query:{buildId:initData.data.recordInfo.id,pointId:this.id,contactName:initData.data.recordInfo.contactName,contactTel:initData.data.recordInfo.contactTel}});
            } else {
              this.render(initData.data);
            }
          } else {
            this.alertMsg(initData.msg);
            return false;
          }
        }).catch((error)=>{
             console.log("error..",error);
        });
    },
    //
    getBuildTypeList(){
      axios.get('/point-evaluation/get-build-type-list').then((res)=>{
        const initData =res.data;
        console.log("",initData);
        if (initData.error_code== 0){
          this.form.buildTypeList=initData.data;//
        } else {
          this.alertMsg(initData.msg);
          return false;
        }
      }).catch((error)=>{
           console.log("error..",error);
      });
    },
    //ID
    getBuildTypeNameById(){
      console.log("list",this.form.buildTypeList);
      for(let i of this.form.buildTypeList){
        if (i.id==this.buildType){
          this.buildTypeValue=i.type_name;
          console.log("buildTypeValue..",this.buildTypeValue);
        }
      }
    },
    checkPhone(value) {
      if (value==="") {
        return true;
      }else{
        var myReg=/^[1][3,4,5,6,7,8,9][0-9]{9}$/;//
        var telReg=/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$///
        if (!myReg.test(value)&&!telReg.test(value)) {
           return false;
        }else{
           return true;
        }
      }
    },
