        this.imgCompany8=data.pointInfo.point_company_pic[0];
      }
      if (data.pointInfo.point_company_pic[1]) {
        this.imgCompany9=data.pointInfo.point_company_pic[1];
      }
      if (data.pointInfo.point_company_pic[2]) {
        this.imgCompany10=data.pointInfo.point_company_pic[2];
      }
      if (data.pointInfo.point_company_pic[3]) {
        this.imgCompany11=data.pointInfo.point_company_pic[3];
      }
      if (data.pointInfo.point_company_pic[4]) {
        this.imgCompany12=data.pointInfo.point_company_pic[4];
      }
      //
      if (data.pointInfo.point_plan[0]) {
        this.imgFlat13=data.pointInfo.point_plan[0];//
      }
      //
      this.renderBuildInfo(data);
    },
    //
    renderBuildInfo(data){
      console.log("data......",data);
      //
      this.form.buildId = data.recordInfo.id; //data.pointInfo.build_record_id;
      this.form.bCircle=data.recordInfo.buildPublicInfo.bCircle;//
      this.form.address=data.recordInfo.address;//
      this.form.contactName=data.recordInfo.contactName;//
      this.form.contactTel=data.recordInfo.contactTel;//
      //
      let specialInfo=data.recordInfo.buildSpecialInfo;
      let publicInfo=data.recordInfo.buildPublicInfo;
      // console.log("publicInfo...",publicInfo);
      const typeName=this.buildTypeValue;
      // console.log("typeName......",typeName);
      if (typeName==""){
        // 2
        this.form.checkBusinessCircle=publicInfo.businessCircle;//
        this.form.checkOfficeRent=specialInfo.officeRent;//
        this.form.checkOfficeProperty=specialInfo.officeProperty;//
        this.form.checkHallArea=specialInfo.hallArea;//
        this.form.checkLobbyHigh=specialInfo.lobbyHigh;//
        this.form.checkFacadeMaterial=specialInfo.facadeMaterial;//
        this.form.checkGroundFloor=specialInfo.groundFloor;//
        this.form.checkAirConditioner=specialInfo.airConditioner;//
        this.form.checkElevatorsNumber=specialInfo.elevatorsNumber;//
        // 3
        this.form.checkPopulationAge=publicInfo.populationAge;//
        this.form.checkScale=publicInfo.scale;//
        this.form.checkRoundBusiness=publicInfo.roundBusiness;//
        this.form.checkCompanySize=specialInfo.companySize;//
        this.form.checkCompanyNature=specialInfo.companyNature;//
        this.form.checkYesOrNotOverThree=specialInfo.yesOrNotOverThree;//
        this.form.checkEquipmentLocation=publicInfo.equipmentLocation;//
        this.form.checkIsMoreEntrance=publicInfo.isMoreEntrance;//
        this.form.checkCoffeeshop=publicInfo.coffeeshop;//

      } else if (typeName==""){
        // 2
        this.park.checkParkBusinessCircle=publicInfo.businessCircle;//
        this.park.checkOfficeRent=specialInfo.officeRent;//
        this.park.checkOfficeProperty=specialInfo.officeProperty;//
        this.park.checkHallArea=specialInfo.hallArea;//
        this.park.checkLobbyHigh=specialInfo.lobbyHigh;//
        this.park.checkFacadeMaterial=specialInfo.facadeMaterial;//
        this.park.checkGroundFloor=specialInfo.groundFloor;//
        this.park.checkAirConditioner=specialInfo.airConditioner;//
        this.park.checkElevatorsNumber=specialInfo.elevatorsNumber;//
        // 3
        this.park.checkParkPopulationAge=publicInfo.populationAge;//
        this.park.checkParkScale=publicInfo.scale;//
        this.park.checkCompanySize=specialInfo.companySize;//
        this.park.checkCompanyNature=specialInfo.companyNature;//
        this.park.checkYesOrNotOverThree=specialInfo.yesOrNotOverThree;//
        this.park.checkParkEquipmentLocation=publicInfo.equipmentLocation;//
        this.park.checkParkIsMoreEntrance=publicInfo.isMoreEntrance;//
        this.park.checkParkCoffeeshop=publicInfo.coffeeshop;//
        this.park.checkParkRoundBusiness=publicInfo.roundBusiness;//

      } else if (typeName==""){
        // 
        this.school.checkSchoolTyle=specialInfo.schoolTyle;//
        this.school.checkNumberOfSchool=specialInfo.numberOfSchool;//
        this.school.checkSchoolPutinNumber=specialInfo.schoolPutinNumber;//
        this.school.checkChargingStandard=specialInfo.chargingStandard;//
        this.school.checkSexRatio=specialInfo.sexRatio;//
        this.school.checkLivingExpenses=specialInfo.livingExpenses;//
        this.school.checkIntramuralCommerce=specialInfo.intramuralCommerce;//
        this.school.checkLessThanThreeThousand=specialInfo.lessThanThreeThousand;//
        //
        this.school.checkBuildingAttribute=specialInfo.buildingAttribute;//
        this.school.checkBuildingArea=specialInfo.buildingArea;//
        this.school.checkBuildingHeight=specialInfo.buildingHeight;//
        this.school.checkSchoolHumanTraffic=publicInfo.humanTraffic;//
        this.school.checkSchoolHasOtherEquipment=publicInfo.hasOtherEquipment;//
        this.school.checkSchoolEquipmentLocation=publicInfo.equipmentLocation;//
        this.school.checkSchoolCoffeeshop=publicInfo.coffeeshop;//
        this.school.checkSchoolIsMoreEntrance=publicInfo.isMoreEntrance;//
      } else if (typeName==""){
        //
        this.company.checkCompanyBusinessCircle=publicInfo.businessCircle;//
        this.company.checkOfficeProperty=specialInfo.officeProperty;//
        this.company.checkHallArea=specialInfo.hallArea;//
        this.company.checkLobbyHigh=specialInfo.lobbyHigh;//
        this.company.checkFacadeMaterial=specialInfo.facadeMaterial;//
        this.company.checkGroundFloor=specialInfo.groundFloor;//
        this.company.checkAirConditioner=specialInfo.airConditioner;//
        this.company.checkElevatorsNumber=specialInfo.elevatorsNumber;//
        this.company.checkCompanyCoffeeshop=publicInfo.coffeeshop;//
        this.company.checkCompanyRoundBusiness=publicInfo.roundBusiness;//
        this.company.checkSameLayer=specialInfo.sameLayer;//
        //
        this.company.companyPersonNum=specialInfo.numberOfCompanies;//
        this.company.checkCompanyNature=specialInfo.companyNature;//
        this.company.checkOfficeRent=specialInfo.officeRent;//
        this.company.checkCompanyEquipmentLocation=publicInfo.equipmentLocation;//
        this.company.checkCompanyPopulationAge=publicInfo.populationAge;//
        this.company.checkCompanyScale=publicInfo.scale;//
        this.company.checkOverTime=specialInfo.overTime;//
        this.company.checkOnDutyMajority=specialInfo.onDutyMajority;//
        this.company.checkServingCoffee=specialInfo.servingCoffee;//
        this.company.checkServingAfternoonTea=specialInfo.servingAfternoonTea;//
        this.company.checkSelfServiceEquipment=specialInfo.selfServiceEquipment;//
      } else if (typeName==""){
        this.hospital.checkHospitalType=specialInfo.hospitalType;//
        this.hospital.medicalNum=specialInfo.medicalNum;//
        this.hospital.receptionNum=specialInfo.receptionNum;//
        this.hospital.checkHospitalBusiness=specialInfo.hospitalBusiness;//
        this.hospital.checkHospitalCoffeeshop=publicInfo.coffeeshop;//
        this.hospital.checkBuildingAttribute=specialInfo.buildingAttribute;//
        this.hospital.checkBuildingHeight=specialInfo.buildingHeight;//
        this.hospital.checkHospitalHumanTraffic=publicInfo.humanTraffic;//
        this.hospital.checkHospitalEquipmentLocation=publicInfo.equipmentLocation;//
        this.hospital.checkHospitalHasOtherEquipment=publicInfo.hasOtherEquipment;//
      } else {
        this.form.checkPopulationAge=publicInfo.populationAge;//
        this.form.checkScale=publicInfo.scale;//
        this.form.checkRoundBusiness=publicInfo.roundBusiness;//checkFloorArea: "",
        this.others.checkFloorArea = specialInfo.checkFloorArea;//
        this.others.checkClothing = specialInfo.checkClothing;//
        this.others.checkCoverPopulation = specialInfo.checkCoverPopulation;//
        this.others.checkFloorHeight = specialInfo.checkFloorHeight;//
        this.others.checkFiftyCoffee = specialInfo.checkFiftyCoffee;//50
      }
      //
      if (data.buildRate!="{}"){
        this.form.firstAppraiser=data.buildRate.rate_id;//
        this.form.preliminaryComments=data.buildRate.rate_status==1?"":"";;//
        this.form.preliminaryCommentsDetail=data.buildRate.rate_info;//
      }
      //
      if (data.recordInfo.buildAppearPic[0]) {
        this.imgInitUrl1 = data.recordInfo.buildAppearPic[0];
      }
      if (data.recordInfo.buildAppearPic[1]) {
        this.imgInitUrl2 = data.recordInfo.buildAppearPic[1];
      }
      //
      if (data.recordInfo.buildHallPic[0]) {
        this.imgInitUrl3 = data.recordInfo.buildHallPic[0];
      }
      if (data.recordInfo.buildHallPic[1]) {
        this.imgInitUrl4 = data.recordInfo.buildHallPic[1];
      }
    },
    rentChange() {
      this.form.rentNum = "";
    },
    goBack() {
      history.pushState(null, null, document.URL);
      this.mapShow = false;
      this.showMain = true;
    },
    clickUploadImg(i) {
      if (this.viewDisabled) {
        return false;
      }
      console.log("img i.",i)
      // console.log(this.buildingImgUrlData,this.hallImgUrlData);
      //
      let _this = this;
      wx.ready(function () {
        wx.checkJsApi({
          jsApiList: ['chooseImage'], // JSJS2,
          success: function(res) {
            console.log('wx res',res);
          }
        });
        let localIdsArr;
        let serverId = [];
        let localIds;
        chooseImage(i);
        function chooseImage(i) {
          wx.chooseImage({
            count: 1, // 9
            sizeType: ['compressed'], // 
            sourceType: ['camera'], // 
            success: function (res) {
              console.log('chooseImage res',res);
              localIds = res.localIds;
              let imgLocalId = localIds[0];
              wx.uploadImage({
                localId: localIds.toString(),
                isShowProgressTip: 1,
                success: function(res) {
                  let mediaid = res.serverId;
                  console.log('mediaid',mediaid);
                  console.log("uploadImage res",res);
                  console.log("imgLocalId..",imgLocalId);
                  if (i<=2){
                    _this["imgInitUrl"+i] = imgLocalId;
                    _this.buildingImgUrlData[i-1] = mediaid;
                  } else if (i<=4){
                    _this["imgInitUrl"+i] = imgLocalId;
                    _this.hallImgUrlData[i-3] = mediaid;
                  } else if (i<=6){
                    _this["imgPositon"+i] = imgLocalId;
                    _this.form.pointPositionPic[i-5] = mediaid;
                  } else if (i==7){
                    _this["imgLicense"+i] = imgLocalId;
                    _this.form.pointLicencePic[i-7] = mediaid;
                  } else if (i<=12){
                    _this["imgCompany"+i] = imgLocalId;
                    _this.form.pointCompanyPic[i-8] = mediaid;
                  } else if (i==13){
                    _this["imgFlat"+i] = imgLocalId;
                    _this.form.pointPlan[i-13] = mediaid;
                  }
                },
                fail: function(data){
                  console.log('error',data);
                }
              })
            },
            fail: function(data){
              console.log('error',data);
            }
          });
        }
      });
    },
    //
    saveData() {
      if (this.buildType==""||this.form.buildId==""||this.form.position==""){
        this.alertMsg("");
        return;
      } else {
        this.submitAction("0");
      }
    },
    createdData() {
      //()
      let buildType = this.buildTypeValue;
      if (this.buildType==""||this.form.buildId==""||this.form.position==""){
        this.alertMsg("");
        return;
      }
      if (!this.$verify.check("public")){
        this.alertMsg('');
        return;
      } else if (this.buildTypeValue==''&&!this.$verify.check("publicSchool")){
        this.alertMsg('');
        return;
      } else if (this.buildTypeValue==''&&!this.$verify.check("publicCompany")){
        this.alertMsg('');
        return;
      } else if (this.buildTypeValue==''&&!this.$verify.check("publicHospital")){
        this.alertMsg('');
        return;
      } else if (!this.checkPhone(this.form.contactTel)){
        this.alertMsg('');
        return;
      } else if (this.buildTypeValue==''&&!this.$verify.check("formOffice")){
        this.alertMsg('');
        return;
      } else if (this.buildTypeValue==''&&!this.$verify.check("formPark")){
        this.alertMsg('');
        return;
      } else if (!this.$verify.check("other")){
        this.alertMsg('');
        return;
      } else {
        if (this.imgInitUrl1==addImgUrl||this.imgInitUrl2==addImgUrl) {
          this.alertMsg('');
          return false;
        }
        if (this.imgInitUrl3==addImgUrl||this.imgInitUrl4==addImgUrl) {
          this.alertMsg('');
          return false;
        }
        if (this.imgPositon5==addImgUrl||this.imgPositon6==addImgUrl) {
          this.alertMsg('');
          return false;
        }
        if (this.imgLicense7==addImgUrl) {
          this.alertMsg('');
          return false;
        }
        if (this.imgCompany8==addImgUrl||this.imgCompany9==addImgUrl||this.imgCompany10==addImgUrl||this.imgCompany11==addImgUrl||this.imgCompany12==addImgUrl) {
          this.alertMsg('');
          return false;
        }
        // if (this.imgFlat13==addImgUrl) {
        //   this.alertMsg('');
        //   return false;
        // }
        this.submitAction("1");
      }
    },
    submitAction(type){
      this.getBuildNameById();
      console.log("this.form.pointName...",this.form.pointName);
      // return false;
      //type 01
      const buildType=this.buildTypeValue;//
       // 
      const conditionInfoBuild={//
        floorArea:this.form.floorArea,//
        buildFloor:this.form.buildFloor,//
        lodgingRatio:this.form.lodgingRatio,//
        useRatio:this.form.useRatio,//
        buildNum:this.form.buildNum//
      }
      const conditionInfoPark={//
        floorParkArea:this.park.floorParkArea,//
        buildParkFloor:this.park.buildParkFloor,//
        lodgingParkRatio:this.park.lodgingParkRatio,//
        useParkRatio:this.park.useParkRatio,//
        coverParkNum:this.park.coverParkNum//
      }
      const otherInfo={//
        unicorn:this.form.unicorn,//
        mobile:this.form.mobile,//
        telecom:this.form.telecom,//
        power:this.form.power,//
        putEnvironment:this.form.putEnvironment,//
        competingGoods:this.form.competingGoods//
      }
      const baseInfo={//
        rentWay:this.form.rentWay,//
        rentNum:this.form.rentNum,//
        electric:this.form.electric,//
        service:this.form.service,//
        total:this.form.total,//
      }
      let params={
        point_id:this.id,//Id()
        point_name:this.form.pointName+this.form.position,
        point_position:this.form.position,//
        point_level: this.pointLevel,//
        point_score:this.pointScore,//
        cooperate:this.form.cooperation,//
        point_status:type,//
        build_type_id: this.buildType,//,
        build_record_id:this.form.buildId,//ID
        point_basic_info: baseInfo,
        point_score_info:"",//()
        point_other_info: otherInfo,//
        // buildAppearPic[0];buildHallPic,buildingImgUrlData,hallImgUrlData
        point_licence_pic:this.form.pointLicencePic,//
        point_position_pic:this.form.pointPositionPic,//
        point_company_pic:this.form.pointCompanyPic,//
        point_plan:this.form.pointPlan,//
        contact_tel:this.form.contactTel,//
        contact_name:this.form.contactName//
      }
      if (buildType==""){
        params.point_score_info=conditionInfoBuild;//
      } else if (buildType==""){
        params.point_score_info=conditionInfoPark;//
      } else if (buildType==""){
        baseInfo.area=this.form.area;//
        baseInfo.floor=this.form.floor;//
        baseInfo.humanTraffic=this.form.humanTraffic;//
      } else if (buildType==""){
        baseInfo.companyType=this.form.companyType;//
        baseInfo.companyNum=this.form.companyNum;//
      } else if (buildType==""){
        baseInfo.hospitalLevel=this.form.hospitalLevel;//
        // baseInfo.receptionNum=this.form.receptionNum;//
        // baseInfo.medicalNum=this.form.medicalNum;//
        // baseInfo.hospitalHuman=this.form.hospitalHuman;//
      }
      console.log("params",params);
      axios.post(this.submitFormUrl,params)
      .then((response)=> {
        let data = response.data;
        console.log("data",data);
        if (data.error_code==0){
          if (type=="0"){
             this.alertMsg('');
          } else {
            this.alertMsg('');
          }
          // this.$router.push('/record');
          window.location.href = "/point-evaluation/index?type=record";
        } else {
           this.alertMsg(data.msg);
        }
      })
      .catch((error)=> {
        this.alertMsg('');
      });
    },
    alertMsg(msg,type)
    {
      // let msgType = type?type:"error";
      MessageBox.alert('',{
        title: '',
        message: msg,
        confirmButtonText:''
      });
    },
    //
    changeBuildType(event,id) {
      if (event!==undefined) {
        this.buildType= event.target.value;
        this.form.buildId="";
      }
      // console.log("id..",id)
      if (id!==undefined) {
        this.buildType= id;
      }
      this.getBuildTypeNameById();
      console.log("this.buildType..",this.buildType);
      axios.get('/point-evaluation/get-build-list?build_type_id='+this.buildType).then((res)=>{
        let initData = res.data;
        console.log("get-build-list data",initData);
        if (initData.error_code ==0){
          if (initData.data.length>0){
             this.form.buildingNameList=initData.data;//
          } else {
            this.alertMsg("");
            this.form.buildingNameList=[];
          }
        } else {
          this.alertMsg("");
          this.form.buildingNameList=[];
        }
      })
      .catch((error)=>{
        console.log("");
        this.form.buildingNameList=[];
      });
    },
    //ID,
    getInfoByBuildingName(){
      axios.get('/point-evaluation/get-build-record-info?record_id='+this.form.buildId).then((res)=>{
        const initData =res.data;
        console.log("",initData);
        if (initData.error_code== 0){
          this.renderBuildInfo(initData.data);//
        } else {
          this.alertMsg(initData.msg);
          return false;
        }
      }).catch((error)=>{
         console.log("error..",error);
      });
    },
    //ID
    getBuildNameById(){
      console.log("this.form.buildingNameList..",this.form.buildingNameList);
      for(let i of this.form.buildingNameList){
        if (i.id==this.form.buildId){
          this.form.pointName=i.value.split('+')[0];
          break;
        }
      }
    },
    goBackMain() {
      this.goBack();
    },
    getQueryString(name) {
        var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
        var r = window.location.search.substr(1).match(reg);
        if (r != null) {
            return decodeURI(r[2])
        }
        return null
    }
  }
}
</script>
